@import url(https://fonts.googleapis.com/css?family=Open+Sans|Oswald|Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  /* background-color: rgb(231, 242, 250); */
  cursor: default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Welcome {
  margin-top: 15vh;
  align-items: center;
  justify-content: center;
  display: flex;
}
.Welcome-box {
  width: 40vw;
  height: 40vh;
  color: rgb(95, 122, 241);
  text-align: center;
  align-items: center;
}
.Welcome-title {
  font-size: calc(10px + 4vmin);
  font-family: "Vibur";
  margin: 0.5em;
}
.Welcome-start {
  font-size: calc(5px + 2vmin);
  background: rgba(111, 139, 233, 0.863);
  color: white;
  text-align: center;
  max-width: 15vw;
  min-width: 10vw;
  font-weight: 600;
  padding: 2%;
  margin: auto;
  border-radius: 25px;
}

.ChoiceAreaBox {
  margin-top: 1rem;
}
.ChoiceArea {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: auto;
}
.Welcome-title {
  font-family: "Marck Script";
  font-weight: 500;
  font-size: 2em;
}
input[type="radio"] {
  visibility: hidden;
}

.ChoiceItems:hover {
  background-color: rgb(255, 216, 109);
  color: cornflowerblue;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  flex: 25% 1;
  max-width: 60%;
}
.ChoiceAreaSubmit {
  display: block;
  margin: 1% auto;
}
.ChoiceItems {
  text-align: center;
  margin: 5px;
  display: inline-block;
  color: white;
  font-size: 90%;
  font-weight: 600;
  padding: 10px;
  flex: 0 0 10vw;
  background-color: rgba(111, 139, 233, 0.863);
  min-width: 7vw;
  align-content: center;
  width: 25vw;
  vertical-align: middle;
  height: 6vh;
}
.SubmitButton {
  text-align: center;
  margin: 1% auto;
  display: block;
  padding: 5px;
  color: white;
  background-color: cornflowerblue;
  width: 20vw;
  height: 7vh;
  font-size: 100%;
  letter-spacing: 1px;
  font-family: Spartan;
  font-weight: 600;
  border-radius: 25px;
}
.SubmitButton:focus {
  outline: 0;
}
.QuestionNumber {
  margin: auto;
  font-weight: 500;
  color: white;
  background-color: rgba(100, 148, 237, 0.342);
  /* align-items: center; */
  font-size: 3rem;
  text-align: center;
  font-family: "Marck Script";
}

.QuestionText {
  margin: 2% auto;
  align-items: center;
  font-weight: 600;
  color: cornflowerblue;
  font-size: 1.5rem;
  text-align: center;
  font-family: "Voltaire";
}

.QuestionTextBox {
  margin: 4% auto;
  align-items: center;
  width: 60vw;
  height: 20%;
}

.results {
  width: 100%;
}
.congratsBanner {
  text-align: center;
  color: rgb(61, 123, 238);
  font-size: 3em;
  font-weight: 600;
}
.singleResultBox {
  width: 40vw;
  height: 70vh;
  margin: auto;
  color: rgb(61, 123, 238);
}
.restaurantTitle {
  margin-top: 0.7em;
  font-size: 1.5em;
  font-weight: 600;
  text-align: center;
}
.restaurantImage {
  width: 100%;
  max-height: 50%;
}
.restaurantDescription {
  margin-top: 1em;
  font-size: 1.1em;
}
.restaurantInfoBox {
  width: 40vw;
  text-align: center;
  display: block;
}
.restaurantMoney {
  width: 20%;
  margin: 5px;
}

.App {
  /* text-align: center; */
  font-family: "Spartan";
  font-size: 80%;
}

.App-logo {
  max-height: 3rem;
  display: inline;
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.App-header {
  background-color: #6b94e6;
  max-height: 7vh;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  color: white;
  align-items: center;
  justify-content: flex-start;
}

.App-link {
  color: #61dafb;
}
.App-title {
  display: inline;
  font-family: "Marck Script";
  font-size: 1.4em;
  font-weight: 500;
}

